:root {
    --primary-color:#8e3200;
    --secondary-color:#d9a566;
    --text-color:#333;
    --background-color:#fff8f0;
    --accent-color:#e76f51;
  }
  
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', Arial, sans-serif;
  }
  
  body {
    background-color: var(--background-color);
    color: var(--text-color);
    line-height: 1.6;
  }
  
  header {
    background-color: var(--primary-color);
    color: white;
    padding: 1rem 0;
    text-align: center;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .logo {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  nav {
    background-color: var(--secondary-color);
    padding: 1rem 0;
  
    ul {
      display: flex;
      justify-content: center;
      list-style-type: none;
  
      li {
        margin: 0 1rem;
  
        a {
          color: var(--text-color);
          text-decoration: none;
          font-weight: bold;
          transition: color 0.3s;
  
          &:hover {
            color: var(--primary-color);
          }
        }
      }
    }
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 2rem;
  }
  
  .hero {
    text-align: center;
    margin-bottom: 3rem;
  
    h1 {
      font-size: 2.5rem;
      margin-bottom: 1rem;
      color: var(--primary-color);
    }
  
    p {
      font-size: 1.2rem;
      max-width: 800px;
      margin: 0 auto;
    }
  }
  
  .featured-recipes {
    margin-bottom: 3rem;
  }
  
  .section-title {
    font-size: 2rem;
    margin-bottom: 1.5rem;
    color: var(--primary-color);
    text-align: center;
  }
  
  .recipe-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 2rem;
  }
  
  .recipe-card {
    background-color: white;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s;
  
    &:hover {
      transform: translateY(-5px);
    }
  }
  
  .recipe-image {
    height: 200px;
    background-color: #ddd;
    background-size: cover;
    background-position: center;
  }
  
  .recipe-content {
    padding: 1.5rem;
  }
  
  .recipe-title {
    font-size: 1.3rem;
    margin-bottom: 0.5rem;
    color: var(--primary-color);
  }
  
  .recipe-movie {
    font-style: italic;
    color: var(--accent-color);
    margin-bottom: 0.5rem;
  }
  
  .recipe-description {
    margin-bottom: 1rem;
  }
  
  .recipe-link {
    display: inline-block;
    padding: 0.5rem 1rem;
    background-color: var(--primary-color);
    color: white;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s;
  
    &:hover {
      background-color: var(--accent-color);
    }
  }
  
  .categories {
    margin-bottom: 3rem;
  }
  
  .category-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1rem;
  }
  
  .category-item {
    background-color: var(--secondary-color);
    padding: 0.7rem 1.5rem;
    border-radius: 20px;
    cursor: pointer;
    transition: background-color 0.3s;
  
    &:hover {
      background-color: var(--primary-color);
      color: white;
    }
  }
  
  footer {
    background-color: var(--primary-color);
    color: white;
    text-align: center;
    padding: 2rem 0;
    margin-top: 3rem;
  }
  
  .social-icons {
    margin-top: 1rem;
  
    span {
      margin: 0 0.5rem;
      font-size: 1.5rem;
    }
  }
  
  @media (max-width: 768px) {
    .recipe-grid {
      grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    }
  
    nav ul {
      flex-direction: column;
      align-items: center;
  
      li {
        margin: 0.5rem 0;
      }
    }
  }
  